import { LocalStorageService } from './../../../core/service/local-storage.service';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { ClassDetail } from 'core/models/classListResponse';
import { EditclasspopupComponent } from './../../../editclasspopup/editclasspopup.component';
import { GalleryviewpopupComponent } from './../../../galleryviewpopup/galleryviewpopup.component';
import { PlaceDetail } from './../../../core/models/allPlacesApiResponse';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { UserSession } from './../../../core/sessions';
import { ModelpopupLrComponent } from './../../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { JoinclasspopupComponent } from './../../../joinclasspopup/joinclasspopup.component';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { HttpUserUtilsService } from '../../../core/http_wrapper/http-user-utils.service';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { Constants, InfiniteScroll } from './../../../core/constants';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpUserAuthUtilsService } from './../../../core/http_wrapper/http-userauth-utils.service';
import { HttpCommonUtilsService } from '../../../core/http_wrapper/http-common-utils.service';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent } from '../../../common/confirmpopup/confirmpopup.component';
import { DomHandler } from 'core/components/common/api';




@Component({
  selector: 'app-trainerclasses',
  templateUrl: './trainerclasses.component.html',
  styleUrls: ['./trainerclasses.component.css']
})
export class TrainerclassesComponent implements OnInit, HttpSuccesFailureResponse {
  upcommingClaasesDetail: ClassDetail[] = [];
  upcommingClassCount = -1;
  allPlacesList: PlaceDetail[] = [];
  @Output() updatecount = new EventEmitter();
  currentPage = 0;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  public isTrainerMapped = false;
  listIsLaoding = false;
  private shouldReset = false;
  isLoaded = false;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;
  channelName:any;
  userAgent:any;
  platformNameString:any;
  browserName:any;
  currency:any;
  isTrainerAllowedToAddEdit = UserSession.isTrainerAllowed();

  constructor(
    private router: Router,
    private http_user: HttpUserUtilsService,
    private http_userauth: HttpUserAuthUtilsService,
    private modal: Modal,
    private _commonHttpService: HttpCommonUtilsService,
    private globalSubscriptionService: GlobalSubscriptionService,
    private _activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();
    this.browserName = Constants.getBrowserName();
    this.userAgent = this.getMobileOperatingSystem();

    this.listIsLaoding = true;
    this.http_user.getTrainerUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, true);
    this.http_userauth.getAllPlaceslist(this, false);
    this._activatedRoute.params.subscribe(params => {
      if ((params['sessionId'] != undefined && params['sessionId'] != '')) {
        this.openSessionPopup(params['sessionId']);
        window['shouldOpen'] = false;
      }
  });
  }
  public refreshList() {
    this.lastScrollDistance = 0
    this.isLoadMoreData = true;
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    this.shouldReset = true;
    this.resetBeforeLoad();
    this.http_user.getTrainerUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, false);
  }
  navigate() {
    this.router.navigate(['class'])
  }

  openSessionPopup(sessionId: string) {
    this.http_userauth.getClassDetailList(sessionId, null, this, true);
  }

  getMobileOperatingSystem() {
    const userAgent = window.navigator.userAgent || window.navigator.vendor;

    // Windows Phone must come first because its UA also contains "Android"
    if (/windows phone/i.test(userAgent)) {
        return 'Windows Phone';
    }

    if (/android/i.test(userAgent)) {
        return 'Android';
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent)) {
        return 'iOS';
    }

    return 'web';
}

getPlatformNameString(platform){
  return Constants.getPlatformNameStringFunction(platform);
}

callLiveVideoTokenAPI(classSessionId,link){
  this.channelName = classSessionId;
  //call live video api and in it's success if token is found , redirect to the url

  if(link.length>0){
   window.open(link, "_blank");
    }
  else{
    if(this.browserName=='chrome' || this.browserName=='safari' || this.browserName=='firefox' || this.browserName=='opera'){
      this.userAgent=='web' ? this.http_userauth.getToken(classSessionId,LocalStorageService.getUserId(),this,true) : alert(Constants.alertVideoBroadcastingUser)
    }
    else{ 
      alert("For seamless experience please download Google Chrome browser. The browser you are using is currently not supported by "+UserSession.getDynamicElements(Constants.GymName));
    }
  }
}
  

  onSuccess(type: any, responsedata: any) {
    if (type === Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;
      
      var temp_list: ClassDetail[] = responsedata.data;
      
      if(this.currentPage == 0 && temp_list[0]!=undefined && temp_list[0].count!=undefined ){
        this.upcommingClassCount = temp_list[0].count;
        this.updatecount.next(this.upcommingClassCount);
      }

      temp_list.forEach(element => {
        if(element.showAvailableSeatType&&element.showAvailableSeatType=='SHOWTHREEANDABOVEAVAILSEAT'&&element.availableSeat>3){
          element.availableSeatText="+3";
        }else{
          element.availableSeatText=element.availableSeat;

        }
        this.upcommingClaasesDetail.push(element);
      });
      if (temp_list !== undefined && temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
      // this.upcommingClassCount = this.upcommingClaasesDetail.length;
       
    }

    if(type === Constants.getTokenAPI) { 
      if (responsedata.data.hasOwnProperty("token")) {
         LocalStorageService.setBroadCastToken(responsedata.data.token);
         LocalStorageService.setChannelName(this.channelName);
         this.router.navigate(['/videobroadcast']);
      }
    }

    if (type === Constants.trainerFilterListApi) {
      if (responsedata.data.hasOwnProperty('toTime')) {
        this.openparticipantslist(responsedata.data);
      } else {
        console.error('Trainer details error.');
      }
    }

    if (type === Constants.allPlacesApi) {
      if (responsedata.data.length === 0) {
        this.isTrainerMapped = false;
      } else {
        this.isTrainerMapped = true;
      }
      LocalStorageService.setIsTrainerMapped(this.isTrainerMapped);
      this.globalSubscriptionService.isTrainerMappepedStatus(LocalStorageService.getIsTrainerMapped());

    }
  }

  openInfo() {
    const message = Constants.deletedClass;
    const title = 'Class Deleted!';
    this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));
  }

  onFailure(type: any, response: any) {

    if (type == Constants.trainerFilterListApi) {
      const message = JSON.parse(response._body).errorMessage;
      if ( message == Constants.deletedClass) {
        this.openInfo();
      }
    }

    if(type === Constants.getTokenAPI) {
      alert(response);
    }

    if (type == Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;
      if (this.currentPage == 0) {
      this.updatecount.next(this.upcommingClassCount);
      }

    }
  }

  addLocation(classDetail: ClassDetail, event: Event) {
    window['isNavigatedFromList']  = true;
    event.stopPropagation();
    event.preventDefault();
    this.router.navigate(['class', classDetail.classId]);
  }

  openparticipantslist(gymSessionObj: ClassDetail[]) {
    const parent = this;
    return this.modal.open(EditclasspopupComponent, overlayConfigFactory({ gymSessionObj, parent }, BSModalContext))
  }

   onScrollDown() {
        if (this.isLoadMoreData && !this.listIsLaoding) {
            this.listIsLaoding = true;
            this.currentPage = this.currentPage + 1;
            this.http_user.getTrainerUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, false);
        }
      }
  resetBeforeLoad() {
    this.currentPage = 0;
    this.isLoadMoreData = true;
    this.upcommingClaasesDetail = [];

  }
}



