import { GlobalSubscriptionService } from './../../../../app/globalSubcription.service';
import { Subscription } from 'rxjs/Subscription';
import { Component, OnInit } from '@angular/core';
import { DialogRef, ModalComponent, CloseGuard } from 'angular2-modal';
import { Modal,BSModalContext} from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { MediaResponseDtoList } from "../../../../core/models/mediaResponseList";
import { Constants } from "../../../../core/constants";
import { HttpUserAuthUtilsService } from "../../../../core/http_wrapper/http-userauth-utils.service";
import { LocalStorageService } from "../../../../core/service/local-storage.service";
import { trigger } from '@angular/animations';

declare var $: any;



export class AdditionPopupWindowData extends BSModalContext {
    public trainerMediaDtoList:MediaResponseDtoList[];
}



@Component({
  selector: 'app-trainergallerypopup',
  templateUrl: './trainergallery.component.html',
  styleUrls: ['./trainergallery.component.css']
})
export class TrainerGallerypopupComponent implements CloseGuard, ModalComponent<AdditionPopupWindowData>,OnInit {

  context: AdditionPopupWindowData;
  showLoader:boolean=false; 
  trainergalleryList:MediaResponseDtoList[];
  deletedtrainergalleryList:MediaResponseDtoList[]=[];
  classBackgroundimagesList:MediaResponseDtoList[]=[];
  fileDroppaImagesList:File[]=[];
  imagePreviewListData:imagePreviewListData[]=[];
  filelength:number;
  previousFileListLength:number=0;
  subscription:Subscription;
  progressValue:any=0;
  isUploadinProgress:boolean=false;
  isErrorInUploaddingPic:boolean=false;

  beforeDismiss(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
  beforeClose(): boolean | Promise<boolean> {
    throw new Error("Method not implemented.");
  }
    public uploadpics = `<div class=\"fileupload\">\n
          <p class=\"filetext marginB_5\"><img src=\"/assets/images/file_icon.svg\" ></p>\n
           <p class=\"filetext\">DRAG AND DROP</p>\n
           <p class=\"filetext marginB_5\">Drop .jpg, .png, or <span class=\"bluetext\">browse your files</span></p>\n
       </div>
     `;
  
  public uploadpicsbefore = `<div class=\"fileupload\">\n
          <p class=\"filetext marginB_5\"><img src=\"/assets/images/file_icon.svg\" ></p>\n
           <p class=\"filetext\">DRAG AND DROP</p>\n
           <p class=\"filetext marginB_5\">Drop .jpg, .png, or <span class=\"bluetext\">browse your files</span></p>\n
       </div>
     `;
  public uploadpicsafter = `<div class=\"fileupload2 pull-left\">\
           <p class=\"filetext marginB_5\"><img src=\"/assets/images/file_icon.svg\" ></p>\
           <p class=\"filetext\">Drag&drop</p>\
           <p class=\"filetext marginB_5\"><span class=\"bluetext\">Or browse</span></p></div>\
           `;


  constructor(private globalSubscriptionService:GlobalSubscriptionService,private http_user: HttpUserAuthUtilsService,public modal: Modal,public dialog: DialogRef<AdditionPopupWindowData>) {
   this.context = dialog.context; 
   this.trainergalleryList =this.context.trainerMediaDtoList
   }

  ngOnInit() {

  }

  ngAfterViewInit(){
       $(".modal-dialog ").css("max-width", "740px");
    }

  closePopup(){
      this.dialog.close();
  }  
  removeFileDroppaList(value,i){
     var index=this.fileDroppaImagesList.indexOf(value.file);
     this.fileDroppaImagesList.splice(index,1);
     this.imagePreviewListData.splice(i,1);
     this.setFileLength();         

    }
    removeImage(value:MediaResponseDtoList){
     var index=this.trainergalleryList.indexOf(value);
     this.trainergalleryList.splice(index,1);
     this.http_user.deletemedia(value.mediaId,this,true);
     this.deletedtrainergalleryList.push(value);     
     
  }
    setFileLength(){
      this.filelength=this.imagePreviewListData.length;

    }
  imagePreviewList(file:File){
      var reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
            this.imagePreviewListData.push({"data":reader.result,"file":file})
            }
    } 
  fileUploaded(success, response, file) {
    
  }
 filesUpdated(files) {
  this.isErrorInUploaddingPic=false;
for (var index = this.previousFileListLength; index < files.length; index++) {
                    this.fileDroppaImagesList.push(files[index]);
                    this.imagePreviewList(files[index]);

     } 
   this.previousFileListLength=files.length; 
  }
  beforeRequest(xhr) { 
    xhr.setRequestHeader("Hello", "");
  }


  beforeFileUpload(formData) { 
    return formData;
  }

  beforeAddFile(file) { 
     if(file.type==""){
     alert("only images are allowed");
       return false;

    }
    else if(file.type=="image/gif"){
     alert("Gif images are not allowed");
      return false;
    }
    else if(file.type.split("/")[0]!="image"){
     alert("only images are allowed");
       return false;

    }
    else{
      return true;
    }  
  
  }
  uploadtrainerGalleryPic(){
    this.isUploadinProgress=true;
    this.isErrorInUploaddingPic=false;
      this.fileDroppaImagesList=[];
      this.imagePreviewListData.forEach(element => {
        this.fileDroppaImagesList.push(element.file);
      });

    this.http_user.uploadMediaMultiple1(LocalStorageService.getUserId(),"UserBackground","user",this.fileDroppaImagesList,this,true);
 
    }
  onSuccess(type: any, responsedata: any){
    if(type == Constants.MultipleFileUploadApi){
    this.isUploadinProgress=false;
    this.globalSubscriptionService.trainerGallerypic(this.trainergalleryList);
    $("body").trigger("click");
    this.closePopup();
    }
    if(type == Constants.MediaDelete){
      this.globalSubscriptionService.trainerGallerypic(this.trainergalleryList);
     this.setFileLength();         
      
    }
    if(type==Constants.FileUploadProgress){
        this.progressValue=responsedata;
    }
      if(type==Constants.FileUploadApi){
        for(let i=0;i<this.imagePreviewListData.length;i++){
          if(this.imagePreviewListData[i].file==responsedata.file){
             this.imagePreviewListData.splice(i,1);
             this.trainergalleryList.push({"mediaId":responsedata.mediaObject.data.id,"mediaUrl":responsedata.mediaObject.data.mediaPath,"mediaThumbNailUrl":responsedata.mediaObject.data.thumbnailPath});
           }
        }
      }
  }
  onFailure(type: any, response: string) {
      if(type == Constants.MultipleFileUploadApi){
      this.isUploadinProgress=false;
      this.isErrorInUploaddingPic=true;
      this.globalSubscriptionService.trainerGallerypic(this.trainergalleryList);

    }
   }
}
 export class imagePreviewListData{
  data:any;
  file:File;
  } 


 
 
 


