import { GalleryviewpopupComponent } from './../../../galleryviewpopup/galleryviewpopup.component';
import { GlobalSubscriptionService } from './../../../app/globalSubcription.service';
import { Subscription } from 'rxjs/Subscription';
import { MediaResponseDtoList } from '../../../core/models/mediaResponseList';
import { element } from 'protractor';
import { LocalStorageService } from './../../../core/service/local-storage.service';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { TrainerDetail, GymBranchResponseList, GymBranchDistinct, TrainerBranchResponseList } from './../../../core/models/trainerDetailResponse';
import { UserLogin } from './../../../core/models/userloginresponse';
import { StorageService } from './../../../core/service/storage.service';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpUserAuthUtilsService } from './../../../core/http_wrapper/http-userauth-utils.service';
import { HttpUserUtilsService } from './../../../core/http_wrapper/http-user-utils.service';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { Constants } from '../../../core/constants';
import { overlayConfigFactory } from 'angular2-modal/src/models/overlay-context';
import { UserdetailspopupComponent } from '../../../trainer/userdetailspopup/userdetailspopup.component';
import { TrainerGallerypopupComponent } from '../../../trainer/trainermodule/trainerpersonaldetail/trainergallery/trainergallery.component';
import { TrainersdetailpopupComponent } from '../../../trainer/trainersdetailpopup/trainersdetailpopup.component';
import { UserSession } from 'core/sessions';
declare var $: any;



@Component({
  selector: 'app-trainerpersonaldetail',
  templateUrl: './trainerpersonaldetail.component.html',
  styleUrls: ['./trainerpersonaldetail.component.css']
})
export class TrainerpersonaldetailComponent implements OnInit, HttpSuccesFailureResponse {
  @Output() coverImage = new EventEmitter();
  @Output() profileImage = new EventEmitter();
  userdata: UserLogin;
  userType: number;
  trainerDetail: TrainerDetail;
  trainerMediaDtoList: MediaResponseDtoList[];
  activitieslist: string[] = [];
  associatedBranchList: TrainerBranchResponseList[] = [];
  gymBranchDistinct: GymBranchDistinct[] = [];
  gymListMap: Map<number, GymBranchDistinct[]> = new Map<number, GymBranchDistinct[]>();
  trainerBio: string;
  subscription: Subscription;
  shareurl: any;
  private gallery = null;


  constructor(private globalSubscriptionService: GlobalSubscriptionService, private http_user: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService, private storageService: StorageService,
    private router: Router, private modal: Modal, private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.subscription = this.globalSubscriptionService.trainerGallerypicState
      .subscribe((trainerpic) => {
        this.getTrainerprofile();

      });
    this.userType = LocalStorageService.getUserType();
    this.getTrainerprofile();
  }
  getTrainerprofile() {
    this.http_userauth.getTrainerProfileDetail(LocalStorageService.getUserId(), this, true);
  }
  onSuccess(type: any, responsedata: any) {

    if (type === Constants.FindTrainerProfile) {
      this.activitieslist = [];
      this.trainerDetail = responsedata;
      this.coverImage.next(this.trainerDetail.coverImage);
      this.profileImage.next(this.trainerDetail.profileImage);
      this.shareurl = UserSession.getDynamicElements(Constants.ShareUrl) + 'trainer/' + this.trainerDetail.id + '/' +
      this.trainerDetail.firstName.replace(/[(+{})]/g, '').replace(/ /g, '');
      if (this.trainerDetail.classCategoryList) {
        this.trainerDetail.classCategoryList.forEach(element => {
          this.activitieslist.push(element.classCategoryName);
        });
      }
      if (this.trainerDetail.mediaResponseDtoList) {
        this.trainerMediaDtoList = this.trainerDetail.mediaResponseDtoList;
        const that = this;
        setTimeout(() => {
            if (that.gallery == null) {
                that.gallery = window['$']('.gallery a').simpleLightbox();
            } else {
                that.gallery.destroy();
                that.gallery = window['$']('.gallery a').simpleLightbox();
            }
        }, 100);
      } else {
         this.trainerMediaDtoList = [];
        }
      this.trainerBio = this.trainerDetail.bio;

      if (this.trainerDetail.trainerBranchResponseList != undefined && this.trainerDetail.trainerBranchResponseList.length > 0) {
        this.associatedBranchList = this.trainerDetail.trainerBranchResponseList;
        this.setupGyminfoOFTrainer(this.associatedBranchList);

      }
      this._activatedRoute.params.subscribe(params => {
        if (params['id'] != undefined && params['id'] != '') {
          if (!(params['sessionId'] != undefined && params['sessionId'] != '') && window['shouldOpen']) {
            if (this.trainerDetail.hasOwnProperty('followerCount')) {
              this.openfollowerspopup(this.trainerDetail.followerCount);
            } else {
              console.error('Followers error.');
            }
            window['shouldOpen'] = false;
          }
        }
      });
    }
  }


  onFailure(type: any, response: string) {
    if (type == Constants.FindTrainerProfile) {
    }

  }
  setupGyminfoOFTrainer(associatedBranchList: TrainerBranchResponseList[]) {
    this.gymBranchDistinct = [];
    const distinctgymid: number[] = [];
    associatedBranchList.forEach(element => {
      if (distinctgymid.indexOf(element.gymId) == -1) {
        distinctgymid.push(element.gymId);
      }
    });
    distinctgymid.forEach(element => {
      const myarray: GymBranchDistinct[] = [];
      for (let i = 0; i < associatedBranchList.length; i++) {
        if (element == associatedBranchList[i].gymId) {
          myarray.push({ 'gymName': associatedBranchList[i].gymName, 'gymId': associatedBranchList[i].gymId, 'localitybranchName': associatedBranchList[i].localityName });
        }
      }
      this.gymListMap.set(distinctgymid.indexOf(element) + 1, myarray);
    });

    this.gymListMap.forEach(element => {
      const gymDetails: string[] = [];
      element.forEach(element => {
        gymDetails.push(element.localitybranchName);
      });

      this.gymBranchDistinct.push({ 'gymName': element[0].gymName, 'gymId': element[0].gymId, 'localitybranchName': gymDetails.join(' ,') })
    });
  }

  // for user folllowers

  openfollowerspopup(value) {
    if (+value > 0) {
      let message = 'users'
      return this.modal.open(TrainersdetailpopupComponent, overlayConfigFactory({ message }, BSModalContext));
    }
  }
  openTrainerGallery(trainerMediaDtoList) {
    return this.modal.open(TrainerGallerypopupComponent, overlayConfigFactory({ trainerMediaDtoList }, BSModalContext))
  }
  navigatetoGym(value) {
    if (this.userType != 2) {
      sessionStorage.setItem('gym_id', value.gymId)
      this.router.navigate(['gym', value, value.gymName.replace(/ /g, '')])
    }
  }

    initializeGallery() {
      window['$']('.gallery a').simpleLightbox();
  }
  gallerryPopup(index) {
    const mediaList = this.trainerDetail.mediaResponseDtoList;
    return this.modal.open(GalleryviewpopupComponent, overlayConfigFactory({ mediaList, index }, BSModalContext));

  }
  copytext() {
      let $temp = $('<input>');
      $('body').append($temp);
      $temp.val($('#myinput').val()).select();
      document.execCommand('copy');
      $temp.remove();
  }
}



