import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-trainermodule',
  templateUrl: './trainermodule.component.html',
  styleUrls: ['./trainermodule.component.css']
})
export class TrainermoduleComponent implements OnInit {
  profileImage: any;
  coverImage: any;
  constructor() { }

  ngOnInit() {
  }
  getCoverImage(event) {
    this.coverImage = event;
  }
  getProfileImage(event) {
    this.profileImage = event;
  }

}



